import React, { useEffect, useState, createRef, useContext } from 'react'

import {
  ContainerMain,
  ContentMain
} from './style'

import MenuTopBar from '../../components/menu'
import {
  MenuLeft,
  MenuRight,

  MenuLeftNameUser,
  NameUser,
} from '../../components/menu/style'

import TapMenuBar from '../../components/tabMenu'

import { Icon } from '../../components/Icons'
import { FaBars, FaUsers } from "react-icons/fa";

import ContactMenuBar from '../../components/contactListMenu'
import LoadingBar from '../../components/loading'
import ContextUser from '../../context/contextUser'

import PainelNovosAmigos from '../../components/painelEncontraAmigos'
import PainelConversas from '../../components/painelConversas'
import PainelNotificacoes from '../../components/painelNotificacoes'
import PainelPerfil from '../../components/painelPerfil'
import { request } from '../../services/api'



function Main(props) {
  const { idUsuario } = props.match.params
  const { state, dispatch } = useContext(ContextUser)
  const { socket } = state


  const [isLoading, setLoading] = useState(true)
  const [menuBarStatus, setMenuBarStatus] = useState(false)
  const [topMenuBarStatus, setTopMenuBarStatus] = useState(false)

  const handleToggleMenuBar = () => (setMenuBarStatus(!menuBarStatus))
  const handleToggleTopMenuBar = () => (setTopMenuBarStatus(!topMenuBarStatus))

  const menuBarDOM = createRef()
  const topMenuBarDOM = createRef()

  useEffect(() => {
    if (!isLoading) {
      if (!menuBarStatus) {
        menuBarDOM.current.style.left = '0%'
      } else if (menuBarStatus) {
        menuBarDOM.current.style.left = '100%'
      }
      if (!topMenuBarStatus) {
        topMenuBarDOM.current.style.width = '0%'
      } else if (topMenuBarStatus) {
        //DEFININDO BREAKPOINTs DO topMenuBar
        if (window.innerWidth > 480 && window.innerWidth < 700) {
          topMenuBarDOM.current.style.width = '70%'
        } else if (window.innerWidth < 480) {
          topMenuBarDOM.current.style.width = '100%'
        } else if (window.innerWidth >= 700) {
          topMenuBarDOM.current.style.width = '35%'
        }
      }
      
    }
  }, [isLoading, menuBarStatus, topMenuBarStatus])

  useEffect(() => {
    request({ url: '/usuarios/' + idUsuario },
      //success  
      function (datas) {
        dispatch({ type: "loadDataUser", payload: datas })
        setLoading(false)
        return;
      },
      //fail
      function (err) {
        props.history.replace('/login')
      },
      //progress
      function () {
        setLoading(true)
      });
      
    }, [])
  const registerMessage = (mensagem) => dispatch({ type: 'setNovaMensagem', payload: mensagem })
  useEffect(()=>{
    if(!isLoading){

      socket.on(`chat.message.${state.usuario._id}`, registerMessage)
      return () => socket.off(`chat.message.${state.usuario._id}`, registerMessage)
    }   
  }, [isLoading])
  const [painel, setPainel] = useState(<PainelNovosAmigos />)
  function handlePainelConversas() {
    setPainel(<PainelConversas />)
    handleToggleTopMenuBar()
  }
  function handlePainelEncontrarAmigos() {
    setPainel(<PainelNovosAmigos />)
    handleToggleTopMenuBar()
  }
  function handlePainelNotificacoes() {
    setPainel(<PainelNotificacoes setPainel={setPainel} />)
    handleToggleTopMenuBar()
  }
  function handlePainelPerfilUser() {
    setPainel(<PainelPerfil setPainel={setPainel} />)
    handleToggleTopMenuBar()
  }
  return (
    <React.Fragment>
      {isLoading ? <LoadingBar /> :
        <ContainerMain>
          <MenuTopBar>
            <MenuLeft>
              <Icon onClick={handleToggleTopMenuBar}>
                <FaBars />
              </Icon>
              <MenuLeftNameUser>
                <NameUser>{state.usuario.nome}</NameUser>
              </MenuLeftNameUser>
            </MenuLeft>
            <MenuRight>
              <Icon onClick={handleToggleMenuBar}>
                <FaUsers />
              </Icon>
            </MenuRight>
          </MenuTopBar>
          <ContactMenuBar
            handleToggleMenuBar={handleToggleMenuBar}
            refDOM={menuBarDOM}
            PainelConversas={PainelConversas}
            setPainel={setPainel}
          />
          <TapMenuBar
            refDOM={topMenuBarDOM}
            handleToggleTopMenuBar={handleToggleTopMenuBar}
            handlePainelConversas={handlePainelConversas}
            handlePainelEncontrarAmigos={handlePainelEncontrarAmigos}
            handlePainelNotificacoes={handlePainelNotificacoes}
            handlePainelPerfilUser={handlePainelPerfilUser}
          />
          <ContentMain>
            {painel}
          </ContentMain>
        </ContainerMain>
      }
    </React.Fragment>
  )
}
export default Main