import React, { useContext } from 'react'
import ContextUser from '../../context/contextUser'
import { useParams } from 'react-router-dom'
import {
  ContainerPainel,
  ContainerNotifi,
  NotifiHeader,
  NotifoHeaderData,
  NotifiBody,
  NotifiBodyText,
  NotifiBodyOption
} from './style'
import {
  ContainerEmply,
  BackContainer,
  EmplyIcon,
  EmplyMessage
} from '../containerEmply'
import { Icon } from '../Icons'
import { FaBell, FaRegBellSlash } from 'react-icons/fa'
import { Button } from '../../components/formFeacture'

import { request } from '../../services/api'
function NotifiList({ notifi }) {
  const params = useParams()
  const { state, dispatch } = useContext(ContextUser)

  function handleConfirmeConvite(e) {
    const nid = e.target.getAttribute('data-id');

    request({ url: "/usuarios/enviar/convite/notif/confirme/", method: "POST", body: JSON.stringify(notifi) },
      //success  
      function (datas) {
        alert(datas.mensagem)
        dispatch({ type: "delete", payload: { id: nid }, field: "notificacoes" })
        dispatch({ type: "createNovoAmigo", payload: datas.usuario })
        console.log(notifi)
        dispatch({ type: "delete", payload: { id: notifi.de }, field: "possivelAmigos" })
      },
      //fail
      function (err) {
        console.log(err)
        alert(err.mensagem)
      },
      //progress
      function () {

      }
    )
  }
  function handleDeclineConvite(e) {
    const nid = e.target.getAttribute('data-id');
    dispatch({ type: "delete", payload: { id: nid }, field: "notificacoes" })
  }
  return (
    <ContainerNotifi>
      <NotifiHeader>
        <Icon><FaBell /></Icon>
        <NotifoHeaderData>{new Date(notifi.data).toLocaleDateString()}</NotifoHeaderData>
      </NotifiHeader>
      <NotifiBody >
        {
          notifi.tipo === "PEDIDO_DE_AMIZADE" ?
            <React.Fragment>
              <NotifiBodyText>
                {notifi.texto}
              </NotifiBodyText>
              <NotifiBodyOption>
                <Button onClick={handleConfirmeConvite} data-id={notifi._id} heightCustom="5" fontSize='.8' bgColor="var(--bg-bar-success)" bgColorVar="var(--bg-bar-success-var)">aceitar</Button>
                <Button onClick={handleDeclineConvite} data-id={notifi._id} heightCustom="5" fontSize='.8' bgColor="var(--bg-bar-opacity-none)" bgColorVar="var(--bg-bar)">recusar</Button>
              </NotifiBodyOption>
            </React.Fragment>
            :
            "HeLLOOO"
        }
      </NotifiBody>
    </ContainerNotifi>
  )
}

export default function (props) {
  const { state } = useContext(ContextUser)
  console.log(state.notificacoes)
  return (
    <ContainerPainel>
      {state.notificacoes.length === 0 ?
        <ContainerEmply>
          <BackContainer>
            <EmplyIcon>
              <Icon size="200" style={{fontSize:200}}>
                <FaRegBellSlash fill="#333"/>
              </Icon>
            </EmplyIcon>
            <EmplyMessage>Nenhuma NotificaÃ§Ã£o disponivel no momento!</EmplyMessage>
          </BackContainer>
        </ContainerEmply>:
      state.notificacoes.map(n => (
        <NotifiList key={n._id} notifi={n} />
      ))}
    </ContainerPainel>
  )
}




