import styled, { createGlobalStyle } from 'styled-components'


export const GlobalStyleMenu = createGlobalStyle`
  *{
    color:#fff;
  }    
`
export const Menu = styled.header`
  position:relative;
  height: 40px;
  min-height:40px;
  max-height:40px;
  width: 100%;
  background-color: var(--bg-bar);
  color:#fff;
  display:flex;
  flex-direction:row;
  flex-wrap:nowrap;
  justify-content:space-between;
  align-items:center;
  padding: 0 10px;
  
`
export const MenuLeft = styled.div`
  display:flex;
  justify-content:center;
  align-items:center;

`
export const MenuLeftAvaterUser = styled(MenuLeft)`
  margin-right:10px;
`
export const MenuLeftNameUser = styled(MenuLeft)`
  flex:1;
  align-items: flex-end;
`
export const NameUser = styled.h3`
  font-weight:bold;
  font-size: 1.1rem

`
export const MenuRight = styled(MenuLeft)`
  
`