import React, { useContext } from 'react'
import {
  ContainerTabMenu,
  TabItem,
  TabMenuHeader,
  TabMenuBody
} from './style'
import { Link } from 'react-router-dom'
import { FaBell, FaRegComments, FaTimes, FaUser, FaUserPlus } from 'react-icons/fa'
import { Icon, Logo, Avatar } from '../Icons'
import { API_INFO } from '../../config'
import ContextUser from '../../context/contextUser'
export default function (props) {
  const { state } = useContext(ContextUser)

  return (
    <ContainerTabMenu ref={props.refDOM}>
      <TabMenuHeader>
        <Avatar size='35'>
          <img src={API_INFO.URL + state.usuario.image} alt="avatar do usuario" />
        </Avatar>
        <Icon onClick={props.handleToggleTopMenuBar}><FaTimes /></Icon>
      </TabMenuHeader>
      <TabMenuBody>
        <TabItem onClick={props.handlePainelConversas}>
          <Icon><FaRegComments /></Icon>
          <Link to="#/conversas">CONVERSAS</Link>
        </TabItem>
        <TabItem onClick={props.handlePainelEncontrarAmigos}>
          <Icon><FaUserPlus /></Icon>
          <Link to="#/encontar-amigos">ENCONTRA AMIGOS</Link>
        </TabItem>
        <TabItem onClick={props.handlePainelNotificacoes}>
          <Icon><FaBell /></Icon>
          <Link to="#/notificacoes">NOTIFICACÃ•ES</Link>
        </TabItem>
        <TabItem onClick={props.handlePainelPerfilUser}>
          <Icon><FaUser/></Icon>
          <Link to="#/perfil">PERFIL</Link>
        </TabItem>
      </TabMenuBody>
    </ContainerTabMenu>
  )
}

