import React, { useState } from 'react'
import { GlobalStyle } from './globalStyle'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'

import Home from "./screens/home";
import Main from './screens/main'
import ScreenLogin from './screens/login'
import ScreenSignup from './screens/signup'
import { ContextUserProvider } from './context/contextUser'
import ToastModel from './components/toast'
import { Link } from 'react-router-dom/cjs/react-router-dom.min'
function  App() {
  const [toastContainer, setToasts] = useState()
  const Toast = new ToastModel(toastContainer, setToasts)
  
  return ( 
    <ContextUserProvider Toast={Toast}>
      <GlobalStyle />
      {toastContainer}
      <Router>
        <Switch >
          <Route path="/" exact={true} render={(router) => {
            const SECRET_USER = window.localStorage.getItem('RED_SHARED_SECRET_USER')
            const DATA_USER = window.localStorage.getItem('RED_SHARED_DATA_USER')

            if (SECRET_USER && DATA_USER) {
              const dataUSer = JSON.parse(DATA_USER)
              return router.history.replace('/usuario/conta/' + dataUSer._id, { id: dataUSer._id, nome: dataUSer.nome })
            }
            return <Home/>
          }} />
          <Route path="/usuario/conta/:idUsuario" component={Main} />
          <Route path="/login"><ScreenLogin /></Route>
          <Route path="/signup"><ScreenSignup /></Route>
          <Route path="*" render={() => (<h2>PAGE NOT FOUND</h2>)} />
        </Switch>
      </Router>
    </ContextUserProvider>
  )
}
export default App