import styled from 'styled-components'



export const ContainerSignup = styled.div`
  position:relative;
  left:0%;
  top:0%;
  width:100%;
  height:100vh;
  display:flex;
  align-items:center;
  flex-direction: column;
  overflow:hidden;
  
  background-color:var(--bg-bar-opacity-none);
`
 export const SignupForm = styled.form.attrs({
   id: 'login-form'
 })`
  position: relative;
  display:flex;
  flex:1;
  flex-direction: column;
  margin-top:30px;
  width: 100%;
  overflow-y:auto;
  padding: 30px;
  &::-webkit-scrollbar{
    width:6px;
    height:6px;
  }
  &::-webkit-scrollbar-thumb{
    background-color:var(--bg-bar)
  }
 `