import styled from 'styled-components'



export const ContainerLoading = styled.div`
  position:absolute;
  width:100%;
  height: 100%;
  background-color: ${({bgColor}) => bgColor ? bgColor : 'var(--bg-bar-opacity-none)'};
  display:flex;
  justify-content: center;
  align-items:center;
  z-index: ${({zIndex}) => zIndex ? zIndex : '999'};
  left:0%;
  top:0%;
`
export const BarLoading = styled.div`
  width:${({imgSize}) => imgSize ? imgSize : '120px'};
  height:${({imgSize}) => imgSize ? imgSize : '120px'};
  background-color:transparent;
  display:center;
  align-items:center;
  justify-content:center;
`
export const ImageLoading = styled.img`
  width:100%;
  height:100%;
  -webkit-animation-name: rodar;
  -webkit-animation-duration: 0.7s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-direction:normal;
`