import styled from 'styled-components'


export const ContainerMenuBar = styled.nav`
  position:absolute;
  top:0%;
  left:0%;
  width:100%;
  height:100vh;
  z-index:100;
  background-color:var( --bg-bar-opacity-none);
  transition:all .2s cubic-bezier(0.77, 0, 0.175, 1);
  display:flex;
  flex-direction: column;
  
`
export const ContactMenuBarHeader = styled.div`
  display:flex;
  justify-content:center;
  align-items:center;
  padding: 0 5px;
`
export const ContactListSearch = styled(ContactMenuBarHeader)`
  flex:1;
`
export const ContactList = styled.div`
  padding: 5px;
  margin: 0 2px;
  display: flex;
  flex-direction: column;
  flex:1;
  cursor:pointer;
  overflow-y:auto;
  &::-webkit-scrollbar{
    width:6px;
    height:6px;
  }
  &::-webkit-scrollbar-thumb{
    background-color:var(--bg-bar);
    border-radius: 5px;
  }
  
`
export const ContactListItem = styled(ContactMenuBarHeader)`
  padding:10px;
  margin-bottom: 2px;
  transition:all .2s cubic-bezier(0.77, 0, 0.175, 1);
  &:hover{
    background-color:var(--bg-bar);
  }
`
export const ContactListItemLeft = styled(ContactMenuBarHeader)``
export const ContactListItemRight = styled(ContactMenuBarHeader)`
  flex:1;
  flex-direction:column;
  align-items:flex-start;
`
export const ContactListTitle = styled.h3`
  font-weight:bold;
  font-size: 1.1rem;
`
export const ContactListLastMsg = styled.p`
  font-weight:normal;
  font-size: 0.9rem
`

