import React, { useContext, useEffect } from 'react'

import {
  ContainerMenuBar,
  ContactMenuBarHeader,
  ContactListSearch,
  ContactList,
  ContactListItem,
  ContactListItemLeft,
  ContactListItemRight,
  ContactListTitle,
  ContactListLastMsg
} from './style'
import { Icon, Avatar } from '../Icons'
import { FaArrowLeft, FaSearch } from 'react-icons/fa'
import { FieldInput, Button } from '../formFeacture'

import ContextUser from '../../context/contextUser'
import { API_INFO } from '../../config'

export default function ContactMenuBar({ refDOM, handleToggleMenuBar, setPainel, PainelConversas }) {
  const { state, dispatch } = useContext(ContextUser)
  const handleSelectContact = (e) => {
    
    console.log(state)
    const uid = e.currentTarget.getAttribute('data-id')
    dispatch({ type: "setContatoSelecionado", payload: {id: uid} })
    handleToggleMenuBar()
    setPainel(<PainelConversas/>)
  }
  useEffect(()=>{},[])
  return (
    <ContainerMenuBar ref={refDOM}>
      <ContactMenuBarHeader>
        <Icon onClick={handleToggleMenuBar}>
          <FaArrowLeft />
        </Icon>
        <ContactListSearch>
          <FieldInput type="search" placeholder="pesquisar contatos..." />
          <Button type="submit">
            <FaSearch />
          </Button>
        </ContactListSearch>
      </ContactMenuBarHeader>
      <ContactList>
        {/**DEPOIS SUB. pussivelAmgos POR amigos */}
        {state.amigos.map((u, key) => (
          <ContactListItem key={u._id} data-id={u._id} onClick={handleSelectContact}>
            <ContactListItemLeft>
              <Avatar size="40">
                <img src={API_INFO.URL + '' + u.image} alt="avatar do contato" />
              </Avatar>
            </ContactListItemLeft>
            <ContactListItemRight>
              <ContactListTitle>
                {u.nome}
              </ContactListTitle>
              <ContactListLastMsg>
                ...
                {/* {u.conversas.length === 0 ? "..." : u.conversas[u.conversas.length - 1].texto} */}
              </ContactListLastMsg>
            </ContactListItemRight>
          </ContactListItem>
        ))}
      </ContactList>
    </ContainerMenuBar>
  )
}