import styled from 'styled-components'


export const ContainerPainel = styled.div`
  position:relative;
  width: 100%;
  height:100%;
  background-color:var(--bg-painel);
  overflow-y:auto;
  display:flex;
  flex-direction: column;
`
export const ContainerNotifi = styled.div`
  display:flex;
  flex-direction: column;
  background-color:var(--bg-painel-card);
  padding: 5px;
  border-radius: 3px;
  margin-bottom: 10px;
`
export const NotifiHeader = styled.div`
  border-bottom: 1px solid var(--bg-painel);
  display:flex;
  justify-content:space-between;
  align-items:center;
`
export const NotifoHeaderData = styled.span`
  font-size: 7pt;
  color:#989898;
  font-weight:normal;
` 
export const NotifiBody = styled.div`

`
export const NotifiBodyText = styled.p`
  font-size:.8rem;
  font-weight:normal;
  color:#fbfbfb;
` 
export const NotifiBodyOption = styled.div`
  display:flex;
  align-items:center;
  justify-content: flex-end;
  padding: 5px;
`