
import styled from 'styled-components'

export const ContanierToast = styled.div`
  position: absolute;
  top:0%;
  left:0%;
  display:flex;
  justify-content: center;
  height:100%;
  width:100%;
  align-items:center;
  background-color: rgba(25,25,25,.4);
  z-index:998;
  opacity: 0;
  transition: all .4s ease;
  
`
export const ToastCard = styled.div`
  min-width:300px;
  max-height: 288px;
  background-color:#fff;
  display:flex;
  flex-direction:column;
  justify-content:center;
  padding: 15px 20px;
  @media screen and (max-width: 480px){
    width:70%;
  }
`
export const ToastHeader = styled.div`
  padding: 3px 5px;
  display: flex;
  align-items: center;
`
export const ToastTitle = styled.h3`
  font-size:1.1rem;
  font-weight: bold;
  color:#333;
`
export const ToastBody = styled.div`
  padding:5px;
  flex: 1;
`
export const ToastInfo = styled.p`
  font-weight: normal;
  font-size: .9rem;
  word-wrap: wrap;
  color:#333;
`
export const ToastFooter = styled(ToastHeader)`

`

export const ToastListButtons = styled.div`
  flex:1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
`
export const ToastButton = styled.button`
  border:none;
  outline:none;
  background-color:#03c3d869;
  padding: 5px 10px;
  margin: 2px;
  font-size: .9rem;
  font-weight:bold;
  color: #008492f0;
  display: flex;
  justify-content:center;
  align-items:center;
  cursor:pointer;
  transition: all .2s ease-in;
  &:hover{
    background-color:#03c3d8fa;
  }
`
