import React from 'react'
import {
  ContainerLoading,
  BarLoading,
  ImageLoading
} from './style'
import image from '../../assets/load1.png'

export default function Loading(props) {
  return (
    <ContainerLoading bgColor={props.bgColor} zIndex={props.zIndex}>
      <BarLoading imgSize={props.imgSize}>
        <ImageLoading src={image} alt="LOADING..." />
      </BarLoading>
    </ContainerLoading>
  )
}