import styled from 'styled-components'

export const ContainerPainel = styled.div`
  display: flex;
  flex-direction: column;
  position:relative;
  top:0%;
  left:0%;
  width: 100%;
  height:100%;
  background-color:var(--bg-painel);
  overflow:hidden;
  overflow-y: auto;
  overflow-x: hidden;
  &::-webkit-scrollbar{
        width:6px;
        height:6px;
    }
  &::-webkit-scrollbar-thumb{
      background-color:var(--bg-bar);
      border-radius:4px;
  }
`
export const PerfilHeader = styled.div`
  display:flex;
  flex-direction: column;

`
export const ImageCardHeader = styled.div`
  display: flex;
  justify-content: center;
  background-color:var(--bg-painel-card);
  border-radius: 50px 50px 0px 0px;
  width: 100%;
  padding: 10px;
  & img{
    width: 10rem;
    height: 10rem;
    border-radius: 50%;
    margin-bottom: -50px;
    border:10px solid var(--bg-painel);
  }
`
export const PerfilRelevancia = styled.div`
  font-size: 1.4rem;
  text-align: center;
  font-weight: bold;
  width: 100%;
  padding: 5px;
  color:#fff;
  margin-top: 15px;
`
export const PerfilBody = styled.div`
  flex:1;
  padding-left: 15px;
  padding-right:15px;
`
export const PerfilBodyCard = styled.div`
  width:100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color:var(--bg-painel-card);
  margin-top: 15px;
  cursor: pointer;
  
`
export const PerfilBodyCardDisplay = styled.div`
  width:100%;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
`
export const PerfilBodyCardConfig = styled.div`
  display:flex;
  justify-content: flex-end;  
  background-color: #2f2f2f;
`
export const PerfilBodyCardItemLabel = styled.div`
  font-size: 1.2rem;
  font-weight: bold;
  color:#fff;
  flex: 1/2;
`
export const PerfilBodyCardItemValue = styled.div`
  flex: 1/2;
  font-size: 1rem;
  font-weight: 600;
  color:#fafafa;
`


export const PerfilFooter = styled.div`
  
`
export const PreviewImageAvavtar = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px 0px;
`
