import styled from "styled-components";

export const Container = styled.div`
 display: flex;
 flex-direction: column;
 width: 100vw;
 height: 100vh;
 background-color:#fff;

`
export const PainelInicial = styled.div`
    height: 300px; 
    background-image: linear-gradient(#ee3d3d ,#ff9292);
    border-radius: 0px 0px 75px 75px;
    box-shadow: 0 0.002rem 2px #ccc;
    & h1{
        font-size: 3rem;
        text-align: center;
        padding-top: 100px;
        color: #333;
        color:#fff;
    }
`
export const Login = styled.div`
    width: 100%;
    position: relative;
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
`
export const ButtonLogin = styled.button`
    width: 250px;
    height: 50px;
    color:#fff;
    background-color:var(--bg-bar-opacity-none);
    border-radius: 50px;
    outline: none;
    border: none;
    font-size: 1.2rem;
    padding: 5px;
    font-weight: 700;
    cursor: pointer;
`
export const ArrowSet = styled.div`
    position: absolute;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    border-top: 25px solid var(--bg-bar-opacity-none);
    top: -20    px;
    animation-name: sticker;
    animation-duration: 0.5s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate;
`