import React, { createRef, useContext, useEffect, useRef, useState } from 'react'
import ContextUser from '../../context/contextUser'
import { Avatar, Icon } from '../Icons'
import { API_INFO } from '../../config'
import { FaPaperPlane, FaRegCommentDots, FaSmile, FaTimes } from "react-icons/fa";
//import EmojiPicker from 'emoji-picker-react'

import {
  GlobalStyleEmojiPicker,
  ContainerPainel,
  ConversaHeader,
  ConversasWrapperHeader,
  ConversasBody,
  ConversaHeaderText,
  ConversaFooter,
  ContainerInputMessage,
  InputFieldMessage,
  ContainerIcons,
  ContainerImojiPicker,

} from './style'
import {
  ContainerEmply,
  BackContainer,
  EmplyIcon,
  EmplyMessage
} from '../containerEmply'
import Message from '../messageList'
export default function () {
  const { state, dispatch } = useContext(ContextUser)
  const { socket } = state
  const [textMsg, setTextMsg] = useState("")
  const [emojiShow, setEmojiShow] = useState(false)
  const bodyDOM = createRef()


  const handleChangeTextMsg = (e) => setTextMsg(e.target.value)

  const handleSendMessage = (e) => {
    e.preventDefault()
    if (textMsg.trim() && state.contatoSelecionado) {
      const sender = state.usuario._id
      const addressee = state.contatoSelecionado._id
      socket.emit(`chat.message`, { texto: textMsg, para: addressee, de: sender })
      setTextMsg("")
    }
  }

  useEffect(() => {
    if (bodyDOM.current) {
      if (bodyDOM.current.scrollHeight > bodyDOM.current.offsetHeight) {
        bodyDOM.current.scrollTop = bodyDOM.current.scrollHeight - bodyDOM.current.offsetHeight
      }
    }
  }, [state.conversas])
  const handelEmojiClick = (e) => { }
  const handleShowEmoji = e => setEmojiShow(true)
  const handleHideEmoji = e => setEmojiShow(false)


  return (
    <ContainerPainel>
      <GlobalStyleEmojiPicker />
      {!state.contatoSelecionado ?
        <ContainerEmply>
          <BackContainer>
            <EmplyIcon>
              <Icon size="200" style={{ fontSize: 200 }}>
                <FaRegCommentDots fill="#333" />
              </Icon>
            </EmplyIcon>
            <EmplyMessage>Nenhum contacto selecionado.</EmplyMessage>
          </BackContainer>
        </ContainerEmply> :
        <React.Fragment>
          <ConversaHeader>
            <ConversasWrapperHeader>
              <Avatar>
                <img src={API_INFO.URL + state.contatoSelecionado.image} alt="image" />
              </Avatar>
              <ConversaHeaderText>{state.contatoSelecionado.nome}</ConversaHeaderText>
            </ConversasWrapperHeader>
            <ConversasWrapperHeader>
              <ConversaHeaderText >Eu</ConversaHeaderText>
              <Avatar>
                <img src={API_INFO.URL + state.usuario.image} alt="image" />
              </Avatar>
            </ConversasWrapperHeader>
          </ConversaHeader>
          <ConversasBody ref={bodyDOM}>
            <Message
              usuario={state.usuario}
              datas={state.contatoSelecionado.conversas} />
          </ConversasBody>
          <ContainerImojiPicker>
            {/* <EmojiPicker 
              onEmojiClick={handelEmojiClick}
              disableSearchBar
              disableSkinTonePicker
            /> */}
          </ContainerImojiPicker>
          <ConversaFooter onSubmit={handleSendMessage}>
            <ContainerInputMessage>
              <InputFieldMessage value={textMsg} onChange={handleChangeTextMsg} />
              <Icon onClick={handleShowEmoji}><FaSmile fill={emojiShow ? "#009688" : "var(--bg-painel-card)"} /></Icon>
              <Icon onClick={handleHideEmoji} size={emojiShow ? '40' : '0'}><FaTimes fill="var(--bg-painel-card)" /></Icon>
            </ContainerInputMessage>
            <ContainerIcons>
              <Icon><FaPaperPlane onClick={handleSendMessage} /></Icon>
            </ContainerIcons>
          </ConversaFooter>
        </React.Fragment>
      }
    </ContainerPainel>
  )
}
