import React from 'react'
import { Link} from "react-router-dom";
import {
    Container,
    ButtonLogin,
    ArrowSet,
    Login,
    PainelInicial
} from './style'
export  default function Home(){
    return(
    <Container>
        <PainelInicial>
            <h1>BEM VINDO AO REDSHARED</h1>
        </PainelInicial>
        <Login>
            <ArrowSet></ArrowSet>
            <Link to="/login">
                <ButtonLogin>Fazer Login no redShared</ButtonLogin> 
            </Link>
        </Login>   
    </Container>)
}