import React, { createRef, useEffect } from 'react'

import {
  ContanierToast,
  ToastCard,
  ToastHeader,
  ToastTitle,
  ToastBody,
  ToastInfo,
  ToastFooter,
  ToastListButtons,
  ToastButton
} from './style'
function Alert(props) {
  const toastDOMRef = createRef()
  useEffect(() => { toastDOMRef.current.style.opacity = '1' }, [])
  const waitUntil = time => new Promise(resolve => setTimeout(() => resolve(), time))
  return (
    <ContanierToast ref={toastDOMRef} >
      <ToastCard style={{ minWidth: props.type !== 'alert' ? '80%' : 300 }}>
        <ToastHeader>
          <ToastTitle>{props.title}</ToastTitle>
        </ToastHeader>
        <ToastBody>
          {props.type !== 'alert' ? props.info : <ToastInfo>{props.info} </ToastInfo>}
        </ToastBody>
        <ToastFooter>
          <ToastListButtons>
            {props.buttons.map((b, key) => (
              <ToastButton
                key={key}
                onClick={(e) => {
                  if (typeof b.onPress === 'function')
                    b.onPress(e)

                  toastDOMRef.current.style.opacity = '0'
                  waitUntil(300).then(() => props.setToast([]))
                }
                }>
                {b.value}
              </ToastButton>
            ))}
          </ToastListButtons>
        </ToastFooter>
      </ToastCard>
    </ContanierToast>
  )
}
export default class Toast {
  constructor(toastContaner, setToast) {
    this.toastContaner = toastContaner;
    this.setToast = setToast
  }
  alert(title, info, ArrayButtons) {
    const buttons = ArrayButtons || []
    this.setToast(
      <Alert
        type="alert"
        setToast={this.setToast}
        title={title}
        info={info}
        buttons={buttons}
      />)
  }
  modal(title, jsx, ArrayButtons) {
    const buttons = ArrayButtons || []
    this.setToast(
      <Alert
        type="model"
        setToast={this.setToast}
        title={title}
        info={jsx}
        buttons={buttons}
      />)
  }
}