import React, { createRef, useContext, useEffect, useState } from 'react'
import {
  ContainerPainel,
  PerfilHeader,
  ImageCardHeader,
  PerfilRelevancia,
  PerfilBodyCard,
  PerfilBodyCardDisplay,
  PerfilBodyCardItemLabel,
  PerfilBodyCardItemValue,
  PerfilBodyCardConfig,
  PerfilBody,
  PerfilFooter,
  PreviewImageAvavtar
} from './style'
import ContextUser from '../../context/contextUser'
import { API_INFO } from '../../config'
import { Icon, Avatar } from '../Icons'
import { FaPencilAlt, FaQuestionCircle, FaUpload } from 'react-icons/fa'
import { FieldInput } from '../formFeacture'
import { request } from '../../services/api'


const waitUntil = time => new Promise(resolve => setTimeout(() => resolve(), time))
export default function () {
  const { state, dispatch } = useContext(ContextUser)
  const { Toast } = state
  let formFileDOM;
  function TampleteUploadImage({ handlePropagateForm }) {
    const [imageSelected, setImageSelected] = useState(null)
    formFileDOM = createRef()
    useEffect(() => {
      if (formFileDOM.current) {
        const form = formFileDOM.current
        // form.avatar_image.addEventListener('selected', ()=>{
        form.avatar_image.onchange = (e) => {
          e.preventDefault()
          const isImage = form.avatar_image.files[0].type.match(/image\/\w+/g) ? true : false;
          if (isImage) {
            const reader = new FileReader()
            reader.readAsDataURL(form.avatar_image.files[0])
            reader.onloadend = (e) => {
              setImageSelected(reader.result)
            }
            reader.onload = (e) => {

            }
            reader.onprogress = (e) => {
              console.log("em progress")
            }

          } else {
            waitUntil(500).then(() => Toast.alert('Aviso de Image', "Seleciona uma image!", [{ value: "Fechar" }]))
          }
        }
        // })
      }
    }, [])

    return (
      <React.Fragment>
        <form ref={formFileDOM}><FieldInput type="file" name="avatar_image" /></form>

        {imageSelected ?
          <PreviewImageAvavtar>
            <Avatar size="150">
              <img src={imageSelected} alt="preview image" />
            </Avatar>
          </PreviewImageAvavtar> : ''}
      </React.Fragment>
    )
  }
  const handleEdit = (e) => {
    const type = e.currentTarget.parentElement.parentElement.getAttribute('data-field')
    switch (type) {
      case 'image':
        Toast.modal("Carregar nova image de capa",
          <TampleteUploadImage />,
          [
            { value: "Cancelar" },
            {
              value: "Salvar",
              onPress: (e) => {
                const formDataUpload = new FormData(formFileDOM.current)
                const imageSize = formDataUpload.get('avatar_image').size
                console.log(imageSize)
                if (imageSize && imageSize < 1024 * 1024 * 3) {
                  const SECRET_USER = window.localStorage.getItem('RED_SHARED_SECRET_USER')
                  fetch('http://localhost:4000/usuarios/upload?type=imgCapa',
                    { body: formDataUpload, method: "POST", headers:{'Authorization': SECRET_USER} })
                    .then((res) => res.json())
                    .then((data) => {
                      console.log(data)
                    })
                } else {
                  waitUntil(500).then(() => Toast.alert('Propriedade de Image', "imagens selecionada muito grande!", [{ value: "Fechar" }]))
                }
              }
            }
          ])
        break;
    }

  }
  return (
    <ContainerPainel>
      <PerfilHeader>
        <ImageCardHeader>
          <img src={API_INFO.URL + state.usuario.image} alt="user image" />
        </ImageCardHeader>
        <PerfilRelevancia>
          RelevÃ¢ncia 0.7%
        </PerfilRelevancia>
      </PerfilHeader>
      <PerfilBody>

        <PerfilBodyCard>
          <PerfilBodyCardDisplay>
            <PerfilBodyCardItemLabel>Nome</PerfilBodyCardItemLabel>
            <PerfilBodyCardItemValue>{state.usuario.nome}</PerfilBodyCardItemValue>
          </PerfilBodyCardDisplay>
          <PerfilBodyCardConfig data-field="nome">
            <Icon><FaPencilAlt onClick={handleEdit} /></Icon>
            <Icon><FaQuestionCircle /> </Icon>
          </PerfilBodyCardConfig>
        </PerfilBodyCard>

        <PerfilBodyCard>
          <PerfilBodyCardDisplay>
            <PerfilBodyCardItemLabel>Imagen de capa</PerfilBodyCardItemLabel>
            <PerfilBodyCardItemValue>
              <Avatar size="30">
                <img src={API_INFO.URL + state.usuario.image} alt="user image" />
              </Avatar>
            </PerfilBodyCardItemValue>
          </PerfilBodyCardDisplay>
          <PerfilBodyCardConfig data-field="image">
            <Icon><FaUpload onClick={handleEdit} /></Icon>
            <Icon><FaQuestionCircle /> </Icon>
          </PerfilBodyCardConfig>
        </PerfilBodyCard>

        <PerfilBodyCard>
          <PerfilBodyCardDisplay>
            <PerfilBodyCardItemLabel>Email</PerfilBodyCardItemLabel>
            <PerfilBodyCardItemValue>{state.usuario.email}</PerfilBodyCardItemValue>
          </PerfilBodyCardDisplay>
          <PerfilBodyCardConfig data-field="email" >
            <Icon><FaPencilAlt onClick={handleEdit} /></Icon>
            <Icon><FaQuestionCircle /> </Icon>
          </PerfilBodyCardConfig>
        </PerfilBodyCard>

        <PerfilBodyCard>
          <PerfilBodyCardDisplay>
            <PerfilBodyCardItemLabel>Telefone</PerfilBodyCardItemLabel>
            <PerfilBodyCardItemValue>{state.usuario.telefone}</PerfilBodyCardItemValue>
          </PerfilBodyCardDisplay>
          <PerfilBodyCardConfig data-field="telefone">
            <Icon><FaPencilAlt onClick={handleEdit} /></Icon>
            <Icon><FaQuestionCircle /> </Icon>
          </PerfilBodyCardConfig>
        </PerfilBodyCard>

        <PerfilBodyCard>
          <PerfilBodyCardDisplay>
            <PerfilBodyCardItemLabel>Nacionalidade</PerfilBodyCardItemLabel>
            <PerfilBodyCardItemValue>{state.usuario.nacionalidade}</PerfilBodyCardItemValue>
          </PerfilBodyCardDisplay>
          <PerfilBodyCardConfig data-field="nacionalidade" >
            <Icon><FaPencilAlt onClick={handleEdit} /></Icon>
            <Icon><FaQuestionCircle /> </Icon>
          </PerfilBodyCardConfig>
        </PerfilBodyCard>

        <PerfilBodyCard>
          <PerfilBodyCardDisplay>
            <PerfilBodyCardItemLabel>Data de nascimento</PerfilBodyCardItemLabel>
            <PerfilBodyCardItemValue>{state.usuario.dataNascimento}</PerfilBodyCardItemValue>
          </PerfilBodyCardDisplay>
          <PerfilBodyCardConfig data-field="dataNascimento">
            <Icon><FaPencilAlt onClick={handleEdit} /></Icon>
            <Icon><FaQuestionCircle /> </Icon>
          </PerfilBodyCardConfig>
        </PerfilBodyCard>

      </PerfilBody>

      <PerfilFooter>

      </PerfilFooter>
    </ContainerPainel>
  )
}