import styled, {createGlobalStyle} from 'styled-components'





export const FieldInput = styled.input.attrs(props => ({
  type: props.type ? props.type : 'text'
}))`
  border:none;
  outline:none;
  width: 100%;
  background-color:#fff;
  border-radius: 4px;
  padding: 5px;
  padding-left:7px;
  font-size:1.1rem;
  color: #333;
  height:${({ heightCustom }) => heightCustom ? heightCustom + 'px' : '30px'};
  &::placeholder{
    color:#aaa;
    font-family:cursive;
    font-weight: 500;
  }
`
export const SelectMenu = styled.select`
  border:none;
  outline:none;
  width: 100%;
  background-color:#fff;
  border-radius: 4px;
  padding: 5px;
  padding-left:7px;
  font-size:1.1rem;
  color: #333;
  height:${({ heightCustom }) => heightCustom ? heightCustom + 'px' : '30px'};
  cursor: pointer;
`
export const SelectMenuItem = styled.option`
  cursor: pointer;
  color: #333;
  font-weight:bold;
`


export const Button = styled.button.attrs(props => ({
  type: props.tipe ? props.tipe : 'button'
}))`
  border:none;
  outline:none;
  background-color:${({ bgColor }) => bgColor ? bgColor : '#aaa'};
  border-radius: 3px;
  padding: 15px 10px;
  margin: 2px;
  height:${({ heightCustom }) => heightCustom ? heightCustom + 'px' : '30px'};
  width:${({ widthCustom }) => widthCustom ? widthCustom + 'px' : ''};
  font-size:${({ fontSize }) => fontSize ? fontSize + 'rem' : '1.2rem'};
  font-weight:bold;
  color: ${({ textColor }) => textColor ? textColor : '#fff'};
  display: flex;
  justify-content:center;
  align-items:center;
  cursor:pointer;
  transition: all .2s ease-in;
  &:hover{
    background-color:${({ bgColorVar }) => bgColorVar ? bgColorVar : '#ccc'};
  }
`

export const FormGroup = styled.div`
  width:100%;
  display: flex;
  flex-direction: column;
  & input{
    
  }
  & label{

  }
  & input[type=button],
  & input[type=submit],
  & button{
    margin-top:15px;
  }
`
export const FormGroupInline = styled.div`
  width:100%;
  display: flex;
  flex-direction: row;
  justify-content:center;
  align-items:center;
  & :first-child{
    flex:1/2;
    padding-right:5px;
  }
  & :last-child{
    flex:1/2;
    padding-left:5px;
  }
`
export const Label = styled.label`
  padding:10px 2px;
  font-size:1rem;
  font-weight: bold;
  color:#fefefe;
`

export const CheckMark = styled.span`
    position:absolute;
    top:0;
    left:0;
    height:20px;
    width:20px;
    border-radius:2px;
    background-color:#bbb;
    border:2px solid #aaa;
    &:after{
        content:'';
        position:absolute;
        display:none;
    }
`
export const ContainerCheckBox = styled.label`
  font-weight:600;
  color:#444;
  margin-right:5px;
  position:relative;
  justify-content:center;
  align-items:center;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor:pointer;
  font-size:10pt;
  --webkit-user-select: none;
  user-select:none;
  & input {
      position:absolute;
      opacity:1;
      width:0;
      height:0;
      cursor:pointer;
  }
  &:hover input ~ span{
      background-color:#ccc;
  }
  & input:checked ~ span{
      background-color:${props => props.color ? props.color : 'var(--bg-painel)'};
  }
    & span:after{
      left:4px;
      top:0;
      width:5px;
      height:10px;
      border:solid white;
      border-width:0 3px 3px 0;
      transform:rotate(45deg)
  }
  & input:checked ~ span:after{
      display:block;
  }
`


//==================SELECT MENU========================

export const SelectContainer = styled.div.attrs({
  className: 'select-container'
})`
    position: relative;
    font-family:Arial, Helvetica, sans-serif;
    /*margin-left:10px;
    margin-right:10px;*/
    width:100%;

    & select {
       display:none
    }
    & .select-selected{
        /*background-color: var(--bg-painel);
        font-weight:600;
        color:#fff;
        padding:.2rem;
        cursor:pointer;
        border:1px solid transparent;
        border-color:transparent transparent rgba(0,0,0,.1);
        width:100%;*/
        border:none;
        outline:none;
        width: 100%;
        background-color:#fff;
        border-radius: 4px;
        padding: 5px;
        padding-left:7px;
        font-size:1.1rem;
        color: #333;
        height:${({ heightCustom }) => heightCustom ? heightCustom + 'px' : '30px'};
        cursor:pointer;
    }
    & .select-selected:after{
        position:absolute;
        content:'';
        top:5px;
        right:10px;
        width:0;
        height:0;
        border:6px solid transparent;
        border: #111 transparent transparent transparent;
    }
    & .select-selected .select-arrow-active:after{
        border:transparent transparent #fff transparent;
        top:7px;
    }
`
export const GlobalStyleSelect = createGlobalStyle`
    .select-hide{
        height:0px;
    }
    .select-items{
        position:absolute;
        background-color:#fff;
        top:120%;
        left:0;
        right:0;
        z-index:99;
        transition: all .3s ease;
        max-height:200px;
        border-radius:0px 0px 3px 3px;
        overflow-y:auto;
        z-index:99;
        margin-left:6px;
        /*box-shadow:0px .08rem 15px #ccc;*/
        
    }
    .select-items div{
        color:#111;
        font-weight:500;
        padding:.3rem .5rem;
        cursor:pointer;
    }
    .select-items::-webkit-scrollbar{
        width:3px;
        height:6px;
    }
    .select-items::-webkit-scrollbar-thumb{
        border-radius:4px;
        background-color:var(--bg-painel)
    }
    .select-items div:hover, .same-as-selected{
       background-color:rgba(0,0,0,.1)
    }
`
export const initSelectScript = () => {
  var x, i, j, selElement, a, b, c;
  x = document.getElementsByClassName('select-container')//container da tag select
  for (i = 0; i < x.length; i++) {

    //como Ã© uma collectionHTML
    selElement = x[i].getElementsByTagName('select')[0] //select propriamente dito

    //para cada  select criar uma div que conterÃ¡ o option selecionado
    a = document.createElement('div')
    a.setAttribute('class', 'select-selected')
    a.innerHTML = selElement.options[selElement.selectedIndex].innerHTML
    x[i].appendChild(a)

    //para cada select cria um novo div que conterÃ¡ a lista de options
    b = document.createElement('div')
    b.setAttribute('class', 'select-items select-hide')
    //iterando o select e introduzindo cada option na div criada (b[select-items])

    //comesou no 1 porque nÃ£o selecionara o primeiro option
    for (j = 0; j < selElement.length; j++) {
      //para cada option no select original cria um novo div no [select-items]
      c = document.createElement('div')
      c.innerHTML = selElement.options[j].innerHTML
      c.setAttribute('data-item-id', j)
      c.addEventListener('click', (e) => {
        //quando um item Ã© clicado atualiza no select orignal e o item selecionado
        var y, i, k, s, h;

        s = e.target.parentElement.parentElement.getElementsByTagName('select')[0]

        h = e.target.parentElement.previousSibling;
        for (i = 0; i < s.length; i++) {

          if (s.options[i].innerHTML === e.target.innerHTML) {
            s.selectedIndex = i;
            h.innerHTML = e.target.innerHTML
            y = e.target.parentElement.getElementsByClassName("same-as-selected")
            for (k = 0; k < y.length; k++) {
              y[k].removeAttribute('class')
            }
            e.target.setAttribute('class', 'same-as-selected')
            break
          }
        }
        h.click()
      })
      b.appendChild(c)
    }
    x[i].appendChild(b)
    a.addEventListener('click', (e) => {
      e.preventDefault()
      closeAllSelect(e.target)
      e.target.nextSibling.classList.toggle('select-hide')
      e.target.classList.toggle('select-arrow-active')
      
      //document.addEventListener('click', (ev)=>{
      // a.nextSibling.classList.toggle('select-hide')
      //document.removeEventListener('click', ()=>{}, false)
      //}, false)
    }, false)
  }
  function closeAllSelect(element) {
    var x, y, i, arrNode = []
    x = document.getElementsByClassName('select-items')
    y = document.getElementsByClassName('select-selected')
    for (i = 0; i < y.length; i++) {
      if (element === y[i]) {
        arrNode.push(i)
      } else {
        y[i].classList.remove('select-arrow-active')
      }
    }
    for (i = 0; i < x.length; i++) {
      if (arrNode.indexOf(i)) {
        x[i].classList.add('select-hide')
      }
    }

  }
}