import React from 'react'
import {
  ContainerMessage,
  MessageGroupItem,
  MessageItem
} from './style'


export default function ({ datas, usuario }) {
  const style = {
    msgGroupItemStyle(msg) {
      return {
        justifyContent: usuario._id == msg.de ? 'flex-end' : 'flex-start',
      }
    },
    msgItemStyle(msg) {
      return {
        backgroundColor: usuario._id == msg.de ? '#589484a4' : '#4a4848b3'
      }
    },
    msgTextStyle: {

    }
  }
  return (
    <ContainerMessage>
      {datas.map((m, key) => (

        <MessageGroupItem
          key={key}
          style={style.msgGroupItemStyle(m)}>
          <MessageItem
            style={style.msgItemStyle(m)}>
            <p style={style.msgTextStyle}>{m.texto}</p>
          </MessageItem>
        </MessageGroupItem>
      ))}
    </ContainerMessage>
  )
}