import React, { useEffect, useContext, useState } from 'react'
import { API_INFO } from '../../config'
import {
  ContainerPainelNewAmigos,
  PainelBody,
  PainelHeader,
  ContainerList,
  ListBody,
  ListBodyAvatar,
  ListBodyInfo,
  ListBodyName,
  ListBodyEmail,
  ListFooter,
  ContainerSearchBar,


} from './style'
import { useHistory, useParams } from 'react-router-dom'
import ContextUser from '../../context/contextUser'

import { Avatar } from '../Icons'

import { Button, FieldInput } from '../formFeacture'
import { FaSearch } from 'react-icons/fa'
import LoadingBar from '../loading'
import { request } from '../../services/api'


function ListNewAmigos({ datas }) {
  const [statusConvidar, setStatusConvidar] = useState("convidar")
  const [loading, setLoading] = useState(true)
  const { state, dispatch } = useContext(ContextUser)
  const { Toast } = state
  function handleConvidar(e) {
    const uid = e.target.getAttribute('data-id')
    const [convidando] = state.possivelAmigos.filter(u => u._id === uid)
    if (convidando) {
      request({ url: '/usuarios/enviar/convite/notif/' + uid },
        //success
        function (datas) {
          setStatusConvidar("convite enviado :)")
        },
        //fail
        function (err, res) {
          Toast.alert("Convidar amigo", err.mensagem, [{ value: "Fechar" }])
          setStatusConvidar("convite nÃ£o enviado :(")
        },
        //progress
        function () {
          setStatusConvidar("a enviar...")
        })
    } else {
      return Toast.alert("Convidar amigo", "Erro ao enviar convite usuario nÃ£o encontrado", [{ value: "Fechar" }])
    }
  }
  function handleVerPerfil(e) {
    alert("VER PERFIL")
  }
  return (
    <ContainerList>
      <ListBody>
        <ListBodyAvatar>
          <Avatar size="60">
            <img src={API_INFO.URL + "" + datas.image} alt="possivel amigo image" />
          </Avatar>
        </ListBodyAvatar>
        <ListBodyInfo>
          <ListBodyName>{datas.nome}</ListBodyName>
          <ListBodyEmail>{datas.email}</ListBodyEmail>
        </ListBodyInfo>
      </ListBody>
      <ListFooter>
        <Button data-id={datas._id} bgColor="#e53935" bgColorVar="#d32f2f" style={{ borderRadius: 0 }} onClick={handleConvidar}>{statusConvidar}</Button>
        <Button data-id={datas._id} bgColor="#ef5350" bgColorVar="#f44336" style={{ borderRadius: 0 }} onClick={handleVerPerfil}>VER PERFIL</Button>
      </ListFooter>
    </ContainerList>
  )
}
export default function (props) {
  const params = useParams()
  const history = useHistory()
  const { state, dispatch } = useContext(ContextUser)
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    console.log("NUMERO DE USUARIOS: ", state.possivelAmigos.length)
    if (state.possivelAmigos.length === 0) {
      request({ url: '/usuarios' },
        //success
        function (datas) {
          console.log(datas)
          dispatch({ type: "setPossivelAmigos", payload: datas.usuarios })
          setLoading(false)
        },
        //fail
        function (err, res) {
          props.history.replace('/usuario/conta/' + params.idUsuario)
        },
        //progress
        function () {
          setLoading(true)
        })
    } else {
      console.log("NÃ‚O PEDIU DADOS!")
      setLoading(false)
    }
  }, [])
  const [texto, setTexto] = useState("")

  const handleChangeSearch = (e) => {
    const texto = e.target.value
    if (texto.trim()) {
      const searched = state.possivelAmigos.filter(u => new RegExp(`${texto}`, 'i').test(u.nome))
      dispatch({ type: "setpossivelAmigosFetched", payload: searched, marge: false })
    }else{
      dispatch({type:"setpossivelAmigosFetched", payload: []})
    }
    if(texto.trim() !== "" && state.possivelAmigosFetched.length === 0 ){
      
    }
    
  }
  const handleSubmitSearch = (e) => {

  }
  return (
    <ContainerPainelNewAmigos>
      <PainelHeader>
        <ContainerSearchBar onSubmit={handleSubmitSearch}>
          <FieldInput type="search" name="q" placeholder="pesquisar contatos..." onChange={handleChangeSearch} />
          <Button tipe="submit"> <FaSearch /> </Button>
        </ContainerSearchBar>
      </PainelHeader>
      <PainelBody>
        {loading ?
          <LoadingBar bgColor="var(--bg-painel)" zIndex="98" imgSize="100px" /> :
          <React.Fragment>
            {state.possivelAmigosFetched.length === 0 ? 
              state.possivelAmigos.map(u => (<ListNewAmigos key={u._id} datas={u} />)) :
              state.possivelAmigosFetched.map(u => (<ListNewAmigos key={u._id} datas={u} />))
            }
          </React.Fragment>
        }
      </PainelBody>
    </ContainerPainelNewAmigos>
  )
}
