import styled from 'styled-components'



export const ContainerPainel = styled.div`
    position:relative;
    top:0%;
    left:0%;
    width: 100%;
    height:100%;
    background-color:var(--bg-painel);
    overflow-y:auto;
    
`
export const ContainerPainelNewAmigos = styled.div`
  width:100%;
  height:100%;
  display: flex;
  flex-direction: column;
`
export const PainelHeader = styled.div``
export const PainelBody = styled.div`
  position:relative;
  background-color:var(--bg-painel);
  flex:1;
  overflow-y: auto;
  padding-left:20px;
  padding-right:20px;
  &::-webkit-scrollbar{
        width:6px;
        height:6px;
    }
  &::-webkit-scrollbar-thumb{
      background-color:var(--bg-bar);
      border-radius:4px;
  }
  @media screen and (max-width: 480px){
    padding-left:5px;
    padding-right:5px;
  }
`
export const ContainerList = styled.div`
  background-color:var(--bg-painel-card);
  display:flex;
  flex-direction:column;
  margin: 5px;
  padding:10px;
`
export const ListBody = styled.div`
  position:relative;
  display:flex;
`
export const ListBodyAvatar = styled.div``
export const ListBodyInfo = styled.div`
  flex:1;
  margin-left: 15px;
  margin-bottom:15px;
`
export const ListBodyName = styled.h3`
  text-align: left;
  font-weight: bold;
  font-size: 1.1rem;
  color:#fafafa;
  padding: 2px;
`
export const ListBodyEmail = styled.h5`
  text-align: left;
  font-weight: normal;
  font-size: .8rem;
  color:#fafafa;
  padding: 2px;
`
export const ListFooter = styled.div`
    display:flex;
    justify-content:flex-end;
    & button{
      text-align: left;
      font-weight: normal;
      font-size: 1rem;
    }
`
export const ContainerSearchBar = styled.form`
  padding:.5rem .5rem .7rem .5rem;
  display:flex;
  align-items:center;
  background-color:#333;
  border-bottom: 1px solid #222;
  & input{
    flex: 1;
    background-color: #f9f9f9;
  }
`