import styled from 'styled-components'








export const ContainerLogin = styled.div`
  position:relative;
  left:0%;
  top:0%;
  width:100%;
  height:100vh;
  display:flex;
  align-items:center;
  flex-direction: column;
  overflow:hidden;
  background-color:var(--bg-bar-opacity-none);
`
 export const LoginForm = styled.form.attrs({
   id: 'login-form'
 })`
  position: relative;
  display:flex;
  flex-direction: column;
  margin-top:100px;
  width: 100%;
  min-height: 400px;
  /*background-color:#fff;*/
  padding: 30px;
 `