export const API_INFO = { URL: `https://redshared.herokuapp.com` }
export const serializeUser = (payload) => {
  try {
    const data = Object.keys(payload).reduce((initUserData, currentValue) => {
      switch (currentValue) {
        case "amigos":
          initUserData.amigos = payload[currentValue]
          break;
        case "conversas":
          initUserData.conversas = payload[currentValue]
          break;
        case "notificacoes":
          initUserData.notificacoes = payload[currentValue]
          break;
        default:
          initUserData.usuario[currentValue] = payload[currentValue]
          break;
      }
      return initUserData
    }, { usuario: {}, amigos: [], conversas: [], notificacoes: [] })
    return data
  } catch (err) {
    return null
  }
}
export const awaitUntil = time => new Promise((res) => setTimeout(()=> res(), time))