import { stat } from 'fs'
import React, { createContext, useReducer } from 'react'
import { serializeUser } from '../config'
import { socket_connector } from '../services/api'
const ContextUser = createContext({})

export const ContextUserProvider = (props) => {
  const initialState = {
    contatoSelecionado: null,
    usuario: null,
    amigos: [],
    conversas: [],
    notificacoes: [],
    possivelAmigos: [],
    possivelAmigosFetched: [],
    Toast: props.Toast,
    novaImageLoading: null,
    socket: socket_connector('/')
  }

  const actions = {
    loadDataUser(state, action) {
      const data = serializeUser(action.payload)
      
      return {
        ...state,
        usuario: data.usuario,
        amigos: data.amigos,
        conversas: data.conversas,
        notificacoes: data.notificacoes
      }
    },
    setPossivelAmigos(state, action) {
      if (action.marge)
        return { ...state, possivelAmigos: [...state.possivelAmigos, ...action.payload] }
      else
        return { ...state, possivelAmigos: [...action.payload] }
    },
    setpossivelAmigosFetched(state, action){
      return { ...state, possivelAmigosFetched: [...action.payload] }
    },
    setContatoSelecionado(state, action) {
      const [amigo] = state.amigos.filter(a => a._id === action.payload.id)
      amigo.conversas = state.conversas.filter(c => amigo._id == c.para || amigo._id == c.de)
      return { ...state, contatoSelecionado: amigo }
    },
    delete(state, action) {
      const po = state[action.field].filter(n => (n._id !== action.payload.id))
      console.log(po)
      return {
        ...state,
        [action.field]: po
      }
    },
    createNovoAmigo(state, action) {
      return { ...state, amigos: [...state.amigos, action.payload] }
    },
    setNovaMensagem(state, action) {
      const setC = state.contatoSelecionado
      return {
        ...state,
        conversas: [...state.conversas, action.payload],
        contatoSelecionado:
          action.payload.de == setC._id || action.payload.para == setC._id ?
            { ...state.contatoSelecionado, conversas: [...state.contatoSelecionado.conversas, action.payload] } :
            state.contatoSelecionado
      }
    },
    setNovaImageLoading(state, action) {
      console.log("form data", action)
      return {
        ...state,
        novaImageLoading: action.payload
      }
    }
  }
  function reducer(state, action) {
    //console.log(action.type)
    const fn = actions[action.type]
    ///console.log(typeof fn)
    return typeof fn === 'function' ? fn(state, action) : state
  }
  const [state, dispatch] = useReducer(reducer, initialState)

  return (
    <ContextUser.Provider value={{ state, dispatch }}>
      {/**AQUI VAI MENSAGEM DE ERRO! */}
      {props.children}
    </ContextUser.Provider>)
}
export default ContextUser






