import styled from 'styled-components'




export const ContainerTabMenu = styled.nav`
  width:35%;
  background-color:var(--bg-bar-opacity-none);
  display: flex;
  position:absolute;
  top:40px;
  left:0%;
  height:100vh;
  flex-direction: column;
  align-items:center;
  text-align:center;
  z-index: 99;
  transition: width .3s cubic-bezier(0.47, 0, 0.75, 0.72);
  overflow-x: hidden;
  overflow-y: hidden;
  @media screen and (max-width: 570px){
    width:100%;
  }
`
export const TabMenuHeader = styled.div`
  padding:.5rem;
  width:100%;
  display:flex;
  justify-content:space-between;
  align-items:center;
`
export const TabMenuBody = styled.div`
  width:100%;
  display:flex;
  flex:1;
  padding:.1rem;
  flex-direction: column;
  justify-content:flex-start;
  overflow-y: auto;
  &::-webkit-scrollbar{
    width:6px;
    height:6px;
  }
  &::-webkit-scrollbar-thumb{
    background-color:var(--bg-bar);
    border-radius: 5px;
  }
`
export const TabItem = styled.div`
  width:inherit;
  display: flex;
  align-items: center;
  transition: all .3s ease; 
  & a{
    background-color: transparent;
    color:#fff;
    font-size: .8rem;
    font-weight:600;    
    padding: 2px 15px;
    text-decoration:none;
    text-align:left;
    flex:1;
  }
  &:hover{
    background-color:#222;
  }
`