import React, { useState, useContext, useEffect } from 'react'
import { Link, useHistory } from 'react-router-dom'
import {
  ContainerLogin,
  LoginForm
} from './style'

import {
  FormGroup,
  Label,
  FieldInput,
  Button
} from '../../components/formFeacture'
import MenuTopBar from '../../components/menu'
import {
  MenuLeft,
  MenuRight,
  //MenuLeftAvaterUser,
  MenuLeftNameUser,
  NameUser,
} from '../../components/menu/style'
import { request } from '../../services/api'

import ContextUser from '../../context/contextUser'

function Login(props) {
  const { state } = useContext(ContextUser)
  const { Toast } = state
  const history = useHistory()
  // const verifyLogin = (datas) => {
  //   if (datas.email.trim() === '') {
  //     alert("Email invalido")
  //     return false
  //   } else if (datas.senha.length < 6) {
  //     alert("define uma senha com mais de 6 caracter")
  //   }
  //   return true
  // }

  const [dataForm, setDataForm] = useState({ senha: "", email: "" })
  const handlechange = (e) => { setDataForm({ ...dataForm, [e.target.name]: e.target.value }) }
  const [btnLoading, setBtnLoading] = useState(false)
  function handleSubmit(e) {
    // if (verifyLogin(dataForm)) {
    //   if (/^\d{9}$/.test(dataForm.email.trim()))
    //     dataForm.email = parseInt(dataForm.email.trim())
    //   console.log(dataForm)
    request({ url: "/usuarios/login", method: "POST", body: JSON.stringify(dataForm) },
      //success
      function (datas) {
        setBtnLoading(false)
        window.localStorage.setItem('RED_SHARED_SECRET_USER', `Bearer ${datas.token}`)
        window.localStorage.setItem('RED_SHARED_DATA_USER', JSON.stringify(datas.atributos))
        history.replace({ pathname: `/usuario/conta/${datas.atributos._id}` })
      },
      //fail
      function (err) {
        setBtnLoading(false)
        console.log("Errr", err)
        Toast.alert("Erro de login", err.mensagem, [{ value: "Fechar" }])
      },
      //progress
      function () {
        setBtnLoading(true)
      })
    //}
  }
  return (
    <ContainerLogin>
      <MenuTopBar >
        <MenuLeft>
          <MenuLeftNameUser>
            <NameUser>Red Shared</NameUser>
          </MenuLeftNameUser>
        </MenuLeft>
        <MenuRight>
          <Link to="/signup">Signup</Link>
        </MenuRight>
      </MenuTopBar>
      <LoginForm>
        <FormGroup>
          <Label>Email ou Telefone</Label>
          <FieldInput name="email" heightCustom="35" onChange={handlechange} placeholder="recomendamos usar o emaill!"/>
        </FormGroup>
        <FormGroup>
          <Label>Senha</Label>
          <FieldInput type="password" name="senha" heightCustom="35" onChange={handlechange} placeholder="O seu passcode"/>
        </FormGroup>
        <FormGroup>
          <Button type="button" onClick={handleSubmit} bgColor="#ff0000" bgColorVar="#fe1835" heightCustom="35">{!btnLoading ? "LOGAR" : "CARREGANDO..."}</Button>
        </FormGroup>
      </LoginForm>
    </ContainerLogin>
  )

}
export default Login
