import { API_INFO } from "../config"
import io from 'socket.io-client'
export const request = async (ops, ...params) => {
  const SECRET_USER = window.localStorage.getItem('RED_SHARED_SECRET_USER')
  const options = typeof ops === 'object' ? ops : {}
  options.method = options.method ? options.method : "GET"

  const url = API_INFO.URL + options.url
  delete options.url
  options.headers = options.headers ?
    { 'Content-Type': 'application/json', 'Authorization': SECRET_USER, ...options.headers } :
    { 'Content-Type': 'application/json', 'Authorization': SECRET_USER }

  var res = null
  try {
    if (typeof params[2] === 'function')
      params[2]()

    res = await fetch(url, options)
    const datas = await res.json()
    if (res.status >= 400)
      return params[1](datas, res);

    if (typeof params[0] === 'function')
      return params[0](datas, res)

  } catch (err) {
    err.mensagem = err.message
    if (typeof params[1] === 'function')
      return params[1](err, res)
  }
}


export const socket_connector = (url, opts) => {
  const SECRET_USER = window.localStorage.getItem('RED_SHARED_SECRET_USER')
  const options = opts || {}
  const socket = io(API_INFO.URL + url, {
    ...opts,
    extraHeaders: { 'authorization': SECRET_USER},
    auth: {}
  });
  socket.on("connect_error", (err) =>{
    console.error(err.message);
  })
  return socket
}