import styled, { createGlobalStyle } from 'styled-components'


export const ContainerPainel = styled.div`
  display: flex;
  flex-direction: column;
  position:relative;
  top:0%;
  left:0%;
  width: 100%;
  height:100%;
  background-color:var(--bg-painel);
  overflow:hidden;
`
export const ConversaHeader = styled.div`
  display:flex;
  align-items:center;
  justify-content:space-between;
  padding: 5px 10px;
  position: relative;
  width:100%;
  left:0%;
  background: linear-gradient(to bottom, var(--bg-painel) , transparent);

`
export const ConversasWrapperHeader = styled.div`
  display:flex;
  justify-content: space-between;
  align-items: center;
`

export const ConversaHeaderText = styled.div`
  font-size: .8rem;
  font-weight: bold;
  color:#fff;
  margin-right: 10px;
  margin-left: 10px;
  cursor: pointer;
 `
export const ConversasBody = styled.div.attrs({
  className:"scroll-conversas-body"
})`
  flex:1;
  padding: 0 15px;
  overflow-y:auto;
  overflow-x: hidden;
  &::-webkit-scrollbar{
        width:6px;
        height:6px;
    }
  &::-webkit-scrollbar-thumb{
      background-color:var(--bg-bar);
      border-radius:4px;
  }
`
export const ConversaFooter = styled.form`
  display:flex;
  width:100%;
  align-items: center;
  background-color:var(--bg-painel-card);
  padding: 10px 2px;
  box-shadow: 0 0.02rem 15px #1a1a1a;
`
export const ContainerInputMessage = styled.div`
  background-color: var(--bg-painel);
  display:flex;
  align-items:center;
  flex: 1;
`
export const InputFieldMessage = styled.input`
  flex: 1;
  width: 100%;
  height: 50px;
  outline:none;
  border:none;
  background-color: var(--bg-painel);
  font-family: cursive;
  font-weight:normal;
  padding: 0 5px;
  font-size: 13pt;
  color:#888;
 
`
export const ContainerIcons = styled.div`
  width: 50px;
  display:flex;
  justify-content:center;
  align-items: center;
`
export const ContainerImojiPicker = styled.div`

`
export const GlobalStyleEmojiPicker = createGlobalStyle`
  aside.emoji-picker-react{
    width: 100%;
    box-shadow:none;
    border-radius:0;
    background-color:transparent;
  }
`