import React, { useState, useContext } from 'react'
import { Link, useHistory } from 'react-router-dom'
import {
  ContainerSignup,
  SignupForm
} from './style'

import {
  FormGroup,
  Label,
  FieldInput,
  FormGroupInline,
  Button,
  SelectMenu,
  SelectMenuItem
} from '../../components/formFeacture'

import MenuTopBar from '../../components/menu'
import {
  MenuLeft,
  MenuRight,
  //MenuLeftAvaterUser,
  MenuLeftNameUser,
  NameUser,
} from '../../components/menu/style'
import { request } from '../../services/api'
import ContextUser from '../../context/contextUser'

function Signup() {
  const { state } = useContext(ContextUser)
  const { Toast } = state

  const history = useHistory()
  const verifySignup = (datas) => {
    if (datas.nome.trim() === '') {
      Toast.alert("Erro de cadastro", "Prencha o campo nome!", [{ value: "Fechar" }])
      return false
    } else if (datas.email.trim() === '' || !/\w{1,}@\w+.com/.test(datas.email)) {
      Toast.alert("Erro de cadastro", "Email invalido", [{ value: "Fechar" }])
      return false
    } else if (datas.senha.length < 6) {
      Toast.alert("Erro de cadastro", "define uma senha com mais de 6 caracter", [{ value: "Fechar" }])
      return false  
    } else if (datas.senha !== datas.confirmaSenha) {
      Toast.alert("Erro de cadastro", "As senha nÃ£o batem!", [{ value: "Fechar" }])
      return false
    } else if (datas.telefone.trim().length !== 9) {
      Toast.alert("Erro de cadastro", "verifica o seu nÃºmero", [{ value: "Fechar" }])
      return false
    } else if (datas.dataNascimento.trim() === '') {
      Toast.alert("Erro de cadastro", "Ensira a sua data de nascimento", [{ value: "Fechar" }])
      return false
    } else if (datas.nacionalidade.trim() === '') {
      Toast.alert("Erro de cadastro", "pertences a que pÃ¡is", [{ value: "Fechar" }])
      return false
    } else if (datas.sexo.trim().length !== 1) {
      Toast.alert("Erro de cadastro", "Cara es homem ou mulher", [{ value: "Fechar" }])
      return false
    }
    return true
  }
  const [dataForm, setDataForm] = useState({
    nome: "",
    email: "",
    senha: "",
    confirmaSenha: "",
    telefone: "",
    dataNascimento: "",
    sexo: "",
    nacionalidade: ""
  })
  const handlechange = (e) => { setDataForm({ ...dataForm, [e.target.name]: e.target.value }) }
  const [btnLoading, setBtnLoading] = useState(false)

  function handleSubmit(e) {
    e.stopPropagation()
    if (verifySignup(dataForm)) {
      request({ url: "/usuarios/", method: "POST", body: JSON.stringify(dataForm) },
        //success
        function (data) {
          setBtnLoading(false)
          window.localStorage.setItem('RED_SHARED_SECRET_USER', `Bearer ${data.token}`)
          window.localStorage.setItem('RED_SHARED_DATA_USER', JSON.stringify(data.atributos))
          history.replace({ pathname: `/usuario/conta/${data.atributos._id}` })
        },
        //fail
        function (err) {
          setBtnLoading(false)
          Toast.alert("Erro de login", err.mensagem, [{ value: "Fechar" }])
        },
        //progress
        function () {
          setBtnLoading(true)
        })
    }
  }
  return (
    <ContainerSignup>
      <MenuTopBar >
        <MenuLeft>
          <MenuLeftNameUser>
            <NameUser>Red Shared</NameUser>
          </MenuLeftNameUser>
        </MenuLeft>
        <MenuRight>
          <Link to="/login">Login</Link>
        </MenuRight>
      </MenuTopBar>
      <SignupForm>
        <FormGroup>
          <Label>Nome</Label>
          <FieldInput name="nome" heightCustom="35" onChange={handlechange} placeholder="nome completo" />
        </FormGroup>
        <FormGroup>
          <Label>Email</Label>
          <FieldInput name="email" heightCustom="35" onChange={handlechange} placeholder="o seu melhor email" />
        </FormGroup>
        <FormGroupInline>
          <FormGroup>
            <Label>Senha</Label>
            <FieldInput type="password" name="senha" heightCustom="35" onChange={handlechange} placeholder="cria uma senha" />
          </FormGroup>
          <FormGroup>
            <Label>Confirma a Senha</Label>
            <FieldInput type="password" name="confirmaSenha" heightCustom="35" onChange={handlechange} placeholder="confirma a senha criada" />
          </FormGroup>
        </FormGroupInline>
        <FormGroup>
          <Label>Telefone</Label>
          <FieldInput type="number" name="telefone" heightCustom="35" onChange={handlechange} placeholder="o nÃºmero do seu telefone" />
        </FormGroup>
        <FormGroupInline>
          <FormGroup>
            <Label>Data De Nascimento</Label>
            <FieldInput type="date" name="dataNascimento" heightCustom="35" onChange={handlechange} placeholder="o nÃºmero do seu telefone" />
          </FormGroup>
          <FormGroup>
            <Label>Sexo</Label>
            <SelectMenu onChange={handlechange} name="sexo" heightCustom="35">
              <SelectMenuItem value="M" >Masculino</SelectMenuItem>
              <SelectMenuItem value="F">Femenino</SelectMenuItem>
            </SelectMenu>
          </FormGroup>
        </FormGroupInline>
        <FormGroup>
          <Label>Nacionalidade</Label>
          <SelectMenu onChange={handlechange} name="nacionalidade" heightCustom="35">
            <SelectMenuItem value="Angola">Angola</SelectMenuItem>
            <SelectMenuItem value="Brasil">Brasil</SelectMenuItem>
            <SelectMenuItem value="Portugal">Portugal</SelectMenuItem>
            <SelectMenuItem value="Mozambique">Mozambique</SelectMenuItem>
          </SelectMenu>
        </FormGroup>
        <FormGroup>
          <Button type="button" onClick={handleSubmit} bgColor="#ff0000" bgColorVar="#fe1835" heightCustom="35">{!btnLoading ? "CRIAR CONTA" : "CARREGANDO..."}</Button>
        </FormGroup>
      </SignupForm>
    </ContainerSignup>
  )

}
export default Signup